<template>
  <div id="app">
    <mt-header title="小游随行" :fixed="true">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <router-view :seller="seller"/>
    <mt-tabbar :fixed="true" ref="tabbar">
      <mt-tab-item id="index">
        <router-link :to="{ name: 'index', params: { userId: 123 }}"
                     class="nav_index" :seller="seller">
          <icon class="nav-icon" name="home" scale="2"></icon>
        </router-link>
      </mt-tab-item>
      <mt-tab-item id="listen">
        <router-link :to="{ name: 'listen', params: { userId: 123 }}">
          <icon class="nav-icon" name="headphones" scale="2"></icon>
        </router-link>
      </mt-tab-item>
      <mt-tab-item id="person">
        <router-link :to="{ name: 'person', params: { userId: 123 }}">
          <icon class="nav-icon" name="user" scale="2"></icon>
        </router-link>
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
import person from '../../components/person.vue'
import index from '../../components/index.vue'
import listen from '../../components/listen.vue'
import { Toast, Header, Tabbar, TabItem } from 'mint-ui'
import thedata from '../../data.json'
import Icon from 'vue-awesome/components/Icon'

export default {
  name: 'App',
  props: [ ],
  data () {
    return {
      seller: thedata
    }
  },
  created () {
  },
  components: {
    person: person,
    index: index,
    listen: listen,
    t_header: Header,
    t_tabbar: Tabbar,
    t_tabitem: TabItem,
    'icon': Icon
  },
  methods: {
    tit: () => {
      Toast('提示信息')
    },
    selectedd: () => {
      console.log('选')
      console.log(this.$refs.tabbar.selected)
    }
  }
}
</script>

<style>
  * {
    margin: 0;
    text-decoration: none;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    width: 100%;
    background-color: #e2e2e2;
  }

  header.tittle {
    height: 50px;
    background-color: #7cffa5;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
  }
  .headerbar{
    /*position: fixed;*/
    /*top: 0;*/
  }
  .bottombar{
    position: fixed;
    bottom: 0;
  }
  .nav-icon{
    display: block;
    color: #74c546;
  }
</style>



