<template>
  <li class="content" @click="showDetail">
    <img src="../pages/index/assets/logo.png" alt="图标文字">
    <p> {{ name }} </p>
  </li>
</template>

<script>
export default {
  name: 'iconn',
  props: {
    name: {
      type: String
    }
  },
  data () {
    return {

    }
  },
  methods: {
    showDetail () {
      console.log('你点击了icon')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  img{
    width: 50px;
    height: 50px;
  }
  .content{
    width: 20%;
    height: 80px;
    overflow: hidden;
    display: inline-block;
    font-size: 16px;
  }
</style>



