import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const state = {
  count: 'vuex test'
}
const mutations = {
  add (state, args) {
    state.count += ' $'
    console.log(args)
  }
}
export default new Vuex.Store({
  state, mutations
})



