<template>
  <div class="nav">
    <imgSwitch></imgSwitch>
    <ul class="nav_list">
      <indexIcon v-for="item in part" :key="item.name" :name="item.name" ></indexIcon>
    </ul>
    <a href="elm.html" class="elm">
      点外卖
    </a>
    <indexAlbum></indexAlbum>
  </div>
</template>

<script>
import imgSwich from './imgSwich.vue'
import indexIcon from './indexIcon.vue'
import indexAlbum from './indexAlbum.vue'
import detailShow from './detailShow.vue'
export default {
  name: 'index',
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      msg: '主页',
      blackShow: false,
      part: [{
        name: '排行榜'
      }, {
        name: '直播'
      }, {
        name: '音客'
      }, {
        name: '热门'
      }, {
        name: '随便听'
      }, {
        name: '娱乐'
      }, {
        name: '购物'
      }, {
        name: '推荐'
      }, {
        name: '其他'
      }, {
        name: '广告'
      }]
    }
  },
  components: {
    'imgSwitch': imgSwich,
    'indexIcon': indexIcon,
    'indexAlbum': indexAlbum,
    'detailShow': detailShow
  },
  methods: {
  },
  created () {
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .nav{
    font-size: 0;
    .nav_list{
      font-size: 12px;
      list-style-type: none;
      padding: 0 10px;
      background-color: #fff;
      margin: 10px 0;
      /*border-top: 1px solid #9d9d9d;*/
    }
  }
  .black{
    position: fixed;
    width: 100%;
    top: 0;
    height: 100px;
    background-color: rgb(230, 202, 255);
  }
  .hello{
    width: 100px;
    height: 100px;
    background-color: #fff;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
  .elm{
    display: block;
    width: 100%;
    height: 60px;
    background-color: #76d4ff;
    color: aliceblue;
    font-size: 16px;
    margin-bottom: 10px;
    line-height: 60;
  }
</style>



