<template>
  <div class="person">
    person {{ msg }}
    <first></first>
  </div>
</template>

<script>
import first from './test/first.vue'
export default {
  name: 'person',
  data () {
    return {
      msg: '非父子间通信'
    }
  },
  components: {
    'first': first
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
  .person
    display: flex
    background-color: #ffbcbe
    font-size: 12px
    margin-top: 40px
</style>



