import Vue from 'vue'
import Router from 'vue-router'
import person from '@/components/person'
import index from '@/components/index'
import listen from '@/components/listen'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/person',
      name: 'person',
      component: person
    },
    {
      path: '/listen',
      name: 'listen',
      component: listen
    }
  ]
})



