<template>
    <div class="album">
      <p class="cover">
        <img src="../pages/index/assets/logo.png" alt="">
      </p>
      <div class="introduce">
        <span class="name"> {{ album.name }} </span>
        <span class="owner"> {{ album.owner }}</span>
        <p class="introduction"> {{ album.introduction }} </p>
      </div>
    </div>
</template>

<script>
export default {
  name: '',
  props: {

  },
  data () {
    return {
      album: {
        name: '西安 - 完全解说',
        owner: '小明',
        coverPath: '../assets/logo.png',
        introduction: '这里是简单的介绍'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @mixin clearfix {
    &:after {
      clear: both;
      content: '.';
      display: block;
      height: 0;
      line-height: 0;
      overflow: hidden;
    }
    *height: 1%;
  }
  .album{
    width: 100%;
    background-color: #fff;
    font-size: 12px;
    padding: 10px;
    margin-bottom: 10px;
    @include clearfix;
    .cover{
      width: 40%;
      height: 160px;
      padding-right: 10px;
      float: left;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .introduce{
      width: 50%;
      float: left;
      .name{
        font-size: 20px;
      }
    }
  }
</style>



