   </mt-swipe-item>
   <mt-swipe-item>
   <img src="../pages/index/assets/logo.png" alt="">
   </mt-swipe-item>
   <mt-swipe-item>
   <img src="../pages/index/assets/logo.png" alt="">
   </mt-swipe-item>
   </mt-swipe>
   </div>
</template>

<script>
import {Swipe, SwipeItem, Navbar, TabItem} from 'mint-ui'
export default {
  name: '',
  data () {
    return {
      msg: ''
    }
  },
  component: {
    't_swipe': Swipe,
    't_swipeitem': SwipeItem,
    't_navbar': Navbar,
    't_tabitem': TabItem
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  div.imgSwitch{
    font-size: 0;
    img {
      height: 200px;
      width: 100%;
    }
  }
  .all{
    height: 200px;
    width: 100%;
    background-color: #bbd7cd;
  }
</style>



